footer {
    background-color:rgb(215, 215, 215);
    border-top: 2px solid rgb(190, 188, 188);
    padding: 0;
    margin: 0;
    color: #608832;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 20px;
}

.copyright {
    font-size: .75em;
    grid-column: 1/-1;
    grid-row: 1/-1;
    justify-self: center;
    align-self: end;
    padding: 2px 0;
}

#smIcon {
    max-width: 2em;
    color: #608832;
    margin: 1em;
}

.iconLeft {
    grid-column: 2/-2;
    grid-row: 1/-1;
    justify-self: start;
}

.iconRight {
    grid-column: 2/-2;
    grid-row: 1/-1;
    justify-self: end;
}


@media only screen and (min-width: 318px) and (max-width: 680px) {

    footer {
        grid-template-rows: 2fr 1fr;
    }
    .iconLeft {
        grid-column: 1/7;
        justify-self: end;
        -webkit-transform: translateX(-10%);
                transform: translateX(-10%);
    }
    
    .iconRight {
        grid-column: 7/-1;
        justify-self: start;
        -webkit-transform: translateX(10);
                transform: translateX(10);
    }

    .copyright {
        align-self: end;
        grid-row: 2;

    }
}


main.about {
    text-align: center;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column: 2 / -2;
}

#team {
    grid-column: 1 / -1;
    justify-self: center;
}

#skyline {
    grid-column: 1 / -1;
    justify-self: center;
    max-width: 65%;
}

.titleAPV {
    grid-column: 1 / -1;
    justify-self: center;
}

#intro {
    font-size: 1em;
    font-style: italic;
    grid-column: 2 / -2;
    justify-self: center;

}

.aboutV {
    grid-column: 3 / 5;

}

.aboutG {
    grid-column: 1 / 3;
}

.background {
    padding: 0 10%;
}

#pageImages {
    max-width: 240px;
}

@media only screen and (min-width: 318px) and (max-width: 905px) {
    .aboutV,
    .aboutG {
        grid-column: 1 / -1;
        padding: 5px 0;
    }
    
    #intro {
        font-size: .8em;
        grid-column: 1 / -1;
        padding: 0 1em;
    }
}

@media only screen and (min-width: 318px) and (max-width: 768px) {
    #skyline {
        display: none;
    }
}
#news {
    display: grid;
    grid-template-areas: "title title title title"
                         "arts arts arts arts";
}

h1.newsTitle {
    text-align: center;
    grid-area: title;
}

.articleList {
    grid-area: arts;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    justify-content: center;
    align-content: center;
}
.cardContainer {
    display: flex;
    flex-direction: column;
    background-color: #f2f5f5;
    color: black;
    border: 2px solid white;
    border-radius: 15px;
    padding: 15px;
    cursor: pointer;
    width: 300px;
    margin: 20px auto;
}

/* .cardContainer:nth-child(2n) {
    background-color: #608832;
    color: white;
}

.cardContainer:nth-child(2n + 1) {
    background-color: rgb(215, 215, 215);
    color: white;
} */

.cardContainer:hover {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
        -webkit-transition: 0.4s ease-out;
        transition: 0.4s ease-out;
}

#cardImage {
    width: 100%;
    height: 200px;
    justify-content: center;
    border-radius: 15px;
}

.cardAction {
    color: black;
}

.cardAction:hover {
    font: outline;
}
.contactCard {
    background-image: url(/static/media/logo.6fee1b2f.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    min-width: 300px;
    min-height: 180px;
    border: 1px solid #608832;
    padding: 5px;
    justify-self: center;
    border-radius: 5px;
}

.contactCard:hover {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
    -webkit-transition: 0.4s ease-out;
    transition: 0.4s ease-out;
}

.personalContactInfo {
    font-size: .8em;
    display: flex;
    justify-content: space-between;
    -webkit-transform: translateY(190%);
            transform: translateY(190%);
    padding: 0 10px;
    margin: 0 5%;
}

a {
    color: #608832;
}

.names {
    text-align: center;
    margin: 0;
    padding: 0 0 10px 0;
}


.der {
    grid-area: vv;
    margin: 10px 0;
}

.dre {
    grid-area: vg;
    margin: 10px 0;
}
#contact {
    display: grid;
    grid-template-areas: "tit tit tit tit"
                            "fm fm fm fm"
                            "vct vct vct vct"
                            "vv vv vg vg";
}

form {
    padding: 0 4em;
    grid-area: fm;
}

.title {
    grid-area: tit;
    text-align: center;
}

#vCardTitle {
    font-size: 1.25em;
    grid-area: vct;
    text-align: center;
}

@media only screen and (min-width: 318px) and (max-width: 905px) {
   form {
       padding: 0 1.5em;
   }
}

@media only screen and (min-width: 318px) and (max-width: 700px) {
    #contact {
         grid-template-areas:    "tit tit"
                                 "fm fm"
                                 "vct vct"
                                 "vg vg"
                                 "vv vv";
     }
}



html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

body {
  font-family: 'Cormorant Garamond', serif;
  color: #608832;
  font-size: 18px;
}

#root {
    min-height: 100%;
    overflow-x: hidden;
    display: grid;
    grid-template-columns: 1fr repeat(12, minMax(1fr, 60px) 1fr);
    grid-gap: 40px;
}

footer {
    margin-top: auto;
    grid-column: 1 / span 15;
}

header {
    width: 100%;
    grid-column: 1 / span 15;
}

@media screen and (min-width: 318px) { 

  #root {
   display: inline;
   display: initial;
   height: 100vh;
   width: 100vw;
   font-size: 18px;
  }

  h1 { 
    font-size: 1.80em;
    margin: 1.5% 0;
  }

}
.portfolio {
    text-align: center;
    display: grid;
    grid-template-areas:    "port cri"
                            "port cri";
}
#fourtyTwoThird {
    max-width: 200px;
    max-height: 200px;
    border: solid black 2px;
}

.port {
    grid-area: port;
    background-color: #608832;
    border: 20px white solid;
    color: white;
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
}

#criteria {
    grid-area: cri;
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
}


@media only screen and (min-width: 318px) and (max-width: 905px) {
    .portfolio {
        grid-template-areas: "port port"
                             "cri cri" ;
    }

    .port {
        border: none;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}
